<template>
  <div>
    <v-back></v-back>
    <v-search></v-search>
    <hr>
    <!-- 传参1 -->
    <ul>
      <li class="item" is="router-link" :to="'/movieDetail?id='+item.id" v-for="item in movies" :key="item.id">
        <p>名称：{{item.name}}</p>
        <p>价格：{{item.price | priceFilter}}</p>
        <p>时间：{{item.time | timeFilter}}</p>
      </li>
    </ul>
    <hr>
    <!-- 传参2 -->
    <ul>
      <li class="item1" v-for="item in movies" :key="item.id" @click="toMovieDetail(item.id)">
        <p>名称：{{item.name}}</p>
        <p>价格：{{item.price | priceFilter}}</p>
        <p>时间：{{item.time | timeFilter}}</p>
      </li>
    </ul>
  </div>
</template>
<script>
import {movie} from "../util/request"
  export default {
    data() {
      return {
        movies: []
      }
    },
    methods: {
      toMovieDetail(id){
        this.$router.push("/movieDetail?id="+id)
      }
    },
    watch: {},
    mounted() {
      movie().then(res=>{
        console.log(res);
        this.movies = res.data.data
      })
    }
  }

</script>
<style scoped>
.item{
  width: 80%;
  margin: 15px auto;
  padding: 15px;
  background: pink;
  display: block;
}
.item1{
    width: 80%;
  margin: 15px auto;
  padding: 15px;
  background: orange;
}
</style>



